import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { IProduct } from '../data/iproduct';
import { ProductsService } from '../products.service';

@Component({
  selector: 'app-overview',
  templateUrl: './overview.page.html',
  styleUrls: ['./overview.page.scss'],
})
export class OverviewPage implements OnInit {
  public viewTimespan = 0;
  public Object = Object;

  asyncProductRange: Promise<Array<any>>; // Promise<Array<IProduct[]>>;
  productSubscription: Subscription;
  constructor(public productsService: ProductsService) {
    this.updateProducts();
    this.productSubscription = productsService.productListChange().subscribe(
      console.log
      // this.productsService.getProductsByTimespan.bind(this, this.viewTimespan)
    );
  }

  updateProducts() {
    this.asyncProductRange = this.productsService.getProductsByTimespan(this.viewTimespan);
    this.asyncProductRange.then(console.log);
  }

  ngOnInit() {
  }
}
